body.home {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: table;
}

.home-container {
  text-align: center;
  display: table-cell;
  vertical-align: middle;
}

.home-content {
  text-align: center;
  display: inline-block;
}

.home-title {
  font-size: 96px;
  margin-bottom: 40px;
}

.quote {
  font-size: 24px;
}